<template>
    <div class="upload-container">
        <h1 class="upload-title">문제 업로드</h1>
        <p class="upload-guide">다운로드한 엑셀 양식에 문제와 답을 작성하고 업로드해주세요. <br>( *.xlsx 확장자만 업로드 가능합니다.)</p>
        <input type="file" @change="handleFileUpload" ref="fileInput" />
        <div v-if="errorMessage" class="errorMessage">{{ errorMessage }}</div>
    </div>
</template>

<script>
  import * as XLSX from 'xlsx';
  
  export default {
    data() {
    return {
      errorMessage: null
    };
  },
    methods: {
        handleFileUpload(event) {
            const files = event.target.files;
            if (!files.length) return;

            const file = files[0];
            if (file.type !== "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet") {
                this.errorMessage = "업로드한 파일이 xlsx 파일이 아닙니다.";
                this.$refs.fileInput.value = null;
                return;
            }

            const reader = new FileReader();
            reader.onload = (e) => {
                const data = e.target.result;
                const workbook = XLSX.read(data, { type: 'binary' });

                const sheetName = workbook.SheetNames[0];
                const sheet = workbook.Sheets[sheetName];
                const json = XLSX.utils.sheet_to_json(sheet);
                this.excelData = json;

                // 1. 데이터를 주어진 틀에 맞게 정리
                const template = {
                    "Comment": null,
                    "문제": null,
                    "보기A": null,
                    "보기B": null,
                    "보기C": null,
                    "보기D": null,
                    "보기E": null,
                    "정답1": null,
                    "정답2": null,
                    "정답3": null,
                    "정답4": null,
                    "정답5": null,
                    "해설": null
                };
                const formattedData = this.excelData.map(item => {
                    return { ...template, ...item };
                });

                // 2. 필수값 확인
                const requiredFields = ["문제", "보기A", "보기B", "보기C", "정답1"];
                let error = false;

                formattedData.forEach(item => {
                    requiredFields.forEach(field => {
                        if (!item[field] || item[field] === " " || item[field] === "") {
                            this.errorMessage = `${field} 필드에 올바르지 않은 값이 있습니다.`;
                            error = true;
                            return;
                        }
                    });
                    if (error) return;

                    // 3. 정답 값 확인
                    const answerFields = ["정답1", "정답2", "정답3", "정답4", "정답5"];
                    answerFields.forEach(field => {
                    const answer = item[field];
                    if (answer && !["A", "B", "C", "D", "E"].includes(answer.toUpperCase())) {
                        this.errorMessage = `${field} 필드에 유효한 정답(A, B, C, D, E)이 아닌 값이 있습니다.`;
                        error = true;
                        return;
                    }
                    });

                    if (error) return;
                });

                if (!error) this.excelData = formattedData;
                console.log(this.excelData);
                this.saveProcess(this.excelData);

            };
            reader.readAsBinaryString(file);
            
        },

        saveProcess(excelData) {
            const name = prompt("데이터의 이름을 입력하세요:");
            if (name && name.trim() !== "") {
            this.saveToDB(name, excelData).then(() => {
                alert("성공적으로 저장되었습니다.");
                this.$router.push('/Quiz');
            }).catch(err => {
                console.error("저장 중 오류 발생:", err);
                alert("저장에 실패하였습니다.");
            });
            } else {
                alert("올바른 이름을 입력하세요.");
            }
        },
        async saveToDB(name, data) {
            // Dexie 인스턴스에 접근
            const db = this.$db;
            
            const entry = {
                name: name,
                data: data,
                inputdate: new Date()
            };

            return db.quizzes.add(entry);
        }
    },
  };
</script>
<style scoped>

.upload-container {
    max-width: 600px;
    margin: 50px auto;
    padding: 30px;
background: #fff;
    border-radius: 8px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.upload-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
}

.upload-guide {
    font-size: 14px;
    margin-bottom: 20px;
    color: #555;
    text-align: center;
}

input[type="file"] {
    font-size: 16px;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

div[v-if="errorMessage"] {
    color: red;
    font-size: 14px;
    font-weight: bold;
    margin-top: 10px;
}

.errorMessage {
    color: red;
    font-weight: bold;
}

</style>
