<template>
  <div class="quiz-container">
    <div class="quiz-control">
      <div class="prev-next">
        <button class="control-btn prev-quiz" @click="goToPreviousQuestion" v-if="currentQuizIndex > 0">← 이전 (P)</button>
        <button class="control-btn next-quiz" @click="nextQuestion"
          v-if="currentQuizIndex < this.quizDetail.data.length - 1">{{ showAnswer ? "다음 문제 → (Enter)"
            : "다음 문제 → (N)" }}</button>
      </div>
      <button class="control-btn stop-quiz-button" @click="exitQuiz">문제 그만풀기 (Q)</button>
    </div>
    <div class="quiz-content">
      <div class="quiz-question">
        <h2>{{ currentQuizIndex + 1 }}. {{ currentQuiz.문제 }}</h2>
        <div class="badge-container" v-if="showAnswer">
          <svg width="50" height="50" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg" class="correct-badge"
            v-if="isCorrect">
            <circle cx="50" cy="50" r="42" fill="black" />
            <circle cx="50" cy="50" r="40" fill="red" />
            <circle cx="50" cy="50" r="40" fill="white" clip-path="url(#lower-half)" />
            <rect x="10" y="45" width="80" height="10" fill="black" />
            <circle cx="50" cy="50" r="10" fill="white" stroke="black" stroke-width="2" />
            <clipPath id="lower-half">
              <rect x="10" y="50" width="80" height="50" />
            </clipPath>
          </svg>
          <span class="incorrect-badge" v-else>✖</span>
        </div>
      </div>
      <div class="quiz-options">
        <ul>
          <li v-for="(option, index) in  shuffledOptions " :key="index" @click="toggleAnswer(option)"
            :style="optionStyle(option)">
            <input type="checkbox" :id="'option' + index" :value="option" :class="{
              'correct-answer': isOptionCorrect(option) && showAnswer,
              'incorrect-answer': !isOptionCorrect(option) && showAnswer && isSelected(option)
            }" disabled="true" :checked="isSelected(option)">
            <label :for="'option' + index" :class="{ selected: isSelected(option) }">{{ indexToAlphabet[index] + ". " +
              option }}</label>
            <span v-if="showAnswer" class="quiz-info">
              {{ infoText(option) }}
            </span>
          </li>
        </ul>
      </div>
      <button class="control-btn submit-quiz" @click="checkAnswer" v-if="!showAnswer">제출 (Enter)</button>
    </div>
    <div class="quiz-footer">
      <span class="end-quiz-text" v-if="currentQuizIndex === this.quizDetail.data.length - 1">마지막 문제입니다.</span>
      <button class="control-btn restart-quiz" @click="restartQuiz"
        v-if="currentQuizIndex === this.quizDetail.data.length - 1">처음부터</button>
    </div>
  </div>
</template>
  
<script>
export default {
  props: {
    shuffleQuiz: {
      type: Boolean,
      required: true
    },
    quizDetail: {
      type: Object,
      required: true
    }
  },
  mounted() {
    // 컴포넌트가 마운트되었을 때 키보드 이벤트 리스너를 추가
    window.addEventListener('keyup', this.handleKeyup);
  },
  beforeUnmount() {
    // 컴포넌트가 파괴되기 전에 키보드 이벤트 리스너를 제거
    window.removeEventListener('keyup', this.handleKeyup);
  },
  data() {
    return {
      quizzesRandomized: this.shuffleArray(this.quizDetail.data),
      currentQuizIndex: 0,
      selectedAnswers: [],
      showAnswer: false,
      isCorrect: false,
      isChangedComment: false,
      indexToAlphabet: ["A", "B", "C", "D", "E"]
    };
  },
  computed: {
    isSingleAnswer() {
      return this.correctAnswersValues.length === 1;
    },
    currentQuiz() {
      return this.quizzesRandomized[this.currentQuizIndex];
    },
    shuffledOptions() {
      let options = ["보기A", "보기B", "보기C", "보기D", "보기E"].filter(option => this.currentQuiz[option]).map(option => this.currentQuiz[option]);
      // shuffleQuiz가 false인 경우 그대로 반환
      if (!this.shuffleQuiz) {
        return options;
      }
      // shuffleQuiz가 true인 경우 배열을 섞어서 반환
      else {
        return options.sort(() => Math.random() - 0.5);
      }
    },
    correctAnswers() {
      if (!this.currentQuiz) return [];
      return ["정답1", "정답2", "정답3", "정답4", "정답5"]
        .filter(answerKey => this.currentQuiz[answerKey])
        .map(answerKey => this.currentQuiz[answerKey]);
    },
    correctAnswersValues() {
      if (!this.currentQuiz) return [];
      return this.correctAnswers
        .map(answerKey => this.currentQuiz["보기" + answerKey]);
    },

  },
  methods: {
    handleKeyup(event) {
      // Enter 키
      if (event.key === 'Enter') {
        if (this.showAnswer) {
          if (this.currentQuizIndex < this.quizDetail.data.length - 1) {
            this.nextQuestion();
          }
        } else {
          this.checkAnswer();
        }
      }
      // 숫자 1-5 키
      else if (['1', '2', '3', '4', '5'].includes(event.key)) {
        const index = parseInt(event.key, 10) - 1; // 0-4 인덱스로 변환
        const option = this.shuffledOptions[index];
        if (option) {
          this.toggleAnswer(option);
        }
      }
      // 알파뱃 A-E 키
      else if (['a', 'b', 'c', 'd', 'e'].includes(event.key)) {
        const index = event.key.charCodeAt(0) - 97; // 0-4 인덱스로 변환
        const option = this.shuffledOptions[index];
        if (option) {
          this.toggleAnswer(option);
        }
      }
      // n키
      else if (event.key === 'n') {
        if (this.currentQuizIndex < this.quizDetail.data.length - 1) {
          this.nextQuestion();
        }
      }
      // p키
      else if (event.key === 'p') {
        if (this.currentQuizIndex > 0) {
          this.goToPreviousQuestion();
        }
      }
      // q키
      else if (event.key === 'q') {
        this.exitQuiz();
      }
    },
    isSelected(option) { // 선택된 옵션이 배열에 포함되어 있는지 확인
      return this.selectedAnswers.includes(option);
    },
    isAtLeastOneOptionSelected() { // 최소 하나 이상의 옵션이 선택되었는지 확인
      return this.selectedAnswers.length > 0;
    },
    isOptionDisabled(option) { // 옵션이 비활성화되었는지 확인
      // 단일 선택 문제이면서 선택된 옵션이 있고 선택된 옵션이 현재 옵션이 아닌 경우 비활성화
      if (this.isSingleAnswer && this.isAtLeastOneOptionSelected() && !this.isSelected(option)) {
        return true;
      }
      // 정답 확인 후에는 옵션을 비활성화
      else if (this.showAnswer) {
        return true;
      }
      else {
        return false;
      }
    },
    // 제출 이후 정답 여부에 따라서 옵션의 스타일을 변경
    optionStyle(option) {
      if (!this.showAnswer) {
        // 현재 옵션이 선택된 옵션일 경우
        if (this.isSelected(option)) {
          return {
            // 밝은 파란색
            backgroundColor: "#a8d2ff",
            cursor: 'pointer',
            color: "#003f83",
            fontWeight: 'bold'  // 볼드체
          };
        } else {
          return {
            cursor: 'pointer'
          };
        }
      }

      if (this.isOptionCorrect(option)) {
        return {
          color: '#3e9c41',  // 밝은 초록색
          backgroundColor: '#d4edda',
          fontWeight: 'bold'  // 볼드체
        };
      } else {
        // 선택한 옵션이 틀린 경우
        if (this.isSelected(option)) {
          return {
            backgroundColor: '#f8d7da',  // 밝은 빨간색
            color: '#f44336'
          };
        }
      }
    },
    infoText(option) {
      // 정답 확인 후, 사용자에게 선택한 문제에 대한 정보를 보여줍니다.
      // 정답을 옳게 선택한 경우 
      if (this.isOptionCorrect(option) && this.isSelected(option)) {
        return "(선택됨, 정답)";
      }
      // 정답이지만 선택하지 않은 경우
      else if (this.isOptionCorrect(option) && !this.isSelected(option)) {
        return "(선택안됨, 정답)";
      }
      // 오답이지만 선택한 경우
      else if (!this.isOptionCorrect(option) && this.isSelected(option)) {
        return "(선택됨, 오답)";
      }
      // 그 외 경우는 표시하지 않음
      else {
        return "";
      }
    },
    shuffleArray(array) {
      let shuffled = [...array];

      // shuffleQuiz가 false인 경우 그대로 반환
      if (!this.shuffleQuiz) return shuffled;

      // shuffleQuiz가 true인 경우 배열을 섞어서 반환
      for (let i = shuffled.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
      }
      return shuffled;
    },
    checkAnswer() {
      this.isCorrect = this.selectedAnswers.sort().toString() === this.correctAnswersValues.sort().toString();
      this.showAnswer = true;
    },
    goToPreviousQuestion() {
      this.currentQuizIndex--;
      this.selectedAnswers = [];
      this.showAnswer = false;
    },

    exitQuiz() {
      this.$emit('close');
    },
    isOptionCorrect(option) {
      return this.correctAnswersValues.includes(option);
    },
    toggleAnswer(option) {
      // 단일 선택 문제이면서 선택된 옵션이 있고 선택된 옵션이 현재 옵션이 아닌 경우 함수 종료
      if ((this.isSingleAnswer && this.isAtLeastOneOptionSelected() && !this.isSelected(option))) {
        return;
      }
      else if (this.showAnswer) { // 정답 확인 후에는 옵션을 비활성화
        return;
      }

      // 선택된 옵션이 배열에 포함되어 있는지 확인
      const index = this.selectedAnswers.indexOf(option);

      if (index === -1) {
        // 선택된 옵션이 배열에 없는 경우 배열에 추가
        this.selectedAnswers.push(option);
      } else {
        // 선택된 옵션이 배열에 이미 포함되어 있는 경우 배열에서 제거
        this.selectedAnswers.splice(index, 1);
      }
    },
    nextQuestion() {
      if (this.isChangedComment) {
        this.updateComment(this.quizDetail.id, this.currentQuiz.문제, this.currentQuiz.Comment);
        this.isChangedComment = false;
      }
      this.currentQuizIndex++;
      this.selectedAnswers = [];
      this.showAnswer = false;
    },
    restartQuiz() {
      if (this.isChangedComment) {
        this.updateComment(this.quizDetail.id, this.currentQuiz.문제, this.currentQuiz.Comment);
        this.isChangedComment = false;
      }
      this.quizzesRandomized = this.shuffleArray(this.quizDetail.data);
      this.currentQuizIndex = 0;
      this.selectedAnswers = [];
      this.showAnswer = false;
    },
    async updateComment(quizId, targetQuestion, newComment) {
      const db = this.$db;

      // 특정 ID를 가진 문제 세트를 가져옵니다.
      const quiz = await db.quizzes.get(quizId);

      console.log(quiz);

      // 특정 문제 오브젝트를 찾습니다.
      const questionToUpdate = quiz.data.find(question => question.문제 == targetQuestion);
      console.log(questionToUpdate);
      // 찾은 문제 오브젝트의 comment 속성을 수정합니다.
      if (questionToUpdate) {
        questionToUpdate.Comment = newComment;

        // 수정된 문제 세트를 다시 저장합니다.
        await db.quizzes.put(quiz);
      } else {
        console.error("해당 문제를 찾을 수 없습니다.");
      }

    }
  }
};
</script>

<style scoped>
.quiz-container {
  display: grid;
  grid-template-rows: auto 1fr auto;
  gap: 1em;
  max-width: 800px;
  margin: 2em auto;
  padding: 1em;
  background: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: 'Noto Sans KR', sans-serif;
}

.quiz-control {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
}

.control-btn {
  padding: 0.8em 1.2em;
  font-size: 1rem;
  color: white;
  background-color: #5A6268;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.prev-next {
  display: flex;
  gap: 1em;
  margin: 0;
}

.control-btn:hover {
  background-color: #4e555b;
}

.prev-quiz {
  background-color: #6c757d;
}

.stop-quiz-button {
  background-color: #dc3545;
}

.stop-quiz-button:hover {
  background-color: #c82333;
}

.quiz-content {
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 1em;
}

.quiz-question {
  position: relative;
  padding: 0 1em;
  background: #ffffff;
  border-radius: 5px;
}

h2 {
  margin: 0;
  font-size: 1.4em;
}

.badge-container {
  position: absolute;
  top: -0.4em;
  left: -0.2em;
}

.correct-badge {
  top: -0.4em;
  left: -0.2em;
}

.incorrect-badge {
  color: #dc3545;
  font-size: 2em;
  margin-left: 0.4em;
}

.quiz-options ul {
  list-style: none;
  padding: 0;
}

.quiz-options li {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 0.5em;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quiz-options li:hover {
  background-color: #f2f2f2;
}

.quiz-options li label {
  display: block;
  padding: 1em;
  cursor: pointer;
}

input[type="checkbox"] {
  display: none;
}



.submit-quiz {
  grid-column: 1 / -1;
  justify-self: center;
  background-color: #007bff;
  color: #fff;
}

.submit-quiz:hover {
  background-color: #0069d9;
}

.quiz-footer {
  display: flex;
  justify-content: space-between;
}

.end-quiz-text {
  align-self: center;
  font-size: 1rem;
  color: #28a745;
}

.restart-quiz {
  background-color: #ffc107;
}

.restart-quiz:hover {
  background-color: #e0a800;
}

.next-quiz {
  margin-left: 0;
  background-color: #28a745;
}

.next-quiz:hover {
  background-color: #218838;
}

.correct-answer {
  background-color: #d4edda;
  /* 밝은 녹색 */
}

.incorrect-answer {
  background-color: #f8d7da;
  /* 밝은 빨간색 */
}

/* 선택된 라벨 스타일 */
.selected label {
  background-color: #e2e3e5;
  /* 선택된 옵션의 배경색 */
  border-color: #d6d8db;
  /* 선택된 옵션의 테두리색 */
}

.quiz-info {
  font-size: 0.8rem;
  margin-left: auto;
  margin-right: 10px;
  width: 100px;
}
</style>
