<template>
  <div class="app-container">
    <div class="top-menu">
      <button @click="toggleSidebar" class="hamburger-btn" v-if="!isSidebarOpen">☰</button>
    </div>
    <!-- 좌측 메뉴 -->
    <div class="sidebar-menu" :class="{ 'open': isSidebarOpen }">
      <button @click="toggleSidebar" class="close-btn">✖</button>
      <button @click="goToHome" :class="{ 'selected-btn': isActive('/') }" class="main-btn">HOME</button>
      <a :href="downloadUrl" download="excel-template.xlsx" class="main-btn download-btn">양식 다운로드</a>
      <button @click="goToUpload" :class="{ 'selected-btn': isActive('/Upload') }" class="main-btn">문제 업로드</button>
      <button @click="goToQuiz" :class="{ 'selected-btn': isActive('/Quiz') }" class="main-btn">문제 풀기</button>
    </div>
    <!-- 라우트 출력 -->
    <div class="content-container">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isSidebarOpen: false
    };
  },
  computed: {
    // computed 속성으로 파일의 URL 계산
    downloadUrl() {
      return require('@/assets/excel-template.xlsx').default;
    }
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen;
    },
    goToHome() {
      this.$router.push('/'); // 루트 경로로 이동하는 메소드
      this.isSidebarOpen = false;
    },
    goToUpload() {
      this.$router.push('/Upload');
      this.isSidebarOpen = false;
    },
    goToQuiz() {
      this.$router.push('/Quiz');
      this.isSidebarOpen = false;
    },
    isActive(route) {
      return this.$route.path === route;
    }
  }
}
</script>

